@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #ffff;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
}

.todo-apps {
    text-align: center;
    max-width: 700px;
    margin: 100px 0;
    padding: 50px;
}

.todo-apps > h1 {
    font-size: 50px;
    font-weight: 600;
    color: #3e3d3d;
}

.todo-apps > p {
    color: #3e3d3d;
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 20px;
}

.todo-form {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin: 20px 0;
}

.todo-form .todo-input {
    width: 500px;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color:#e5e5e2;
}

.todo-form .todo-btn {
    border: none;
    border-radius: 5px;
    background-color: #7697A0;
    color: #fff;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    padding: 10px 20px;
}

.todo-container{
    background-color: #7697A0;
    border-radius: 10px;
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 600;
    padding: 10px 20px;
    margin-bottom: 10px;
    justify-content: space-between;
    height: auto;
}

.todo-container .text-kegiatan {
    cursor: pointer;
    text-align: left;
    padding: 10px 0;
    overflow: auto;
    margin-right: 15px;
    letter-spacing: 1px;
}


.todo-container.active {
    background-color: #3d4f54;
}

.btn-primary {
    display: flex;
    gap: 15px;
}

.btn-primary .btn-delete , .btn-edit , .btn-complete{
    font-size: 20px;
    cursor: pointer;
}
/* pseudo class */

.todo-btn:hover {
    background-color: #546c72;
    transition: 0.5s;
}

.todo-input:focus {
    outline-color: #7697A0;
    background-color: white;
    transition: 0.5s;
}

@media only screen and (max-width: 600px) {
    .todo-apps {
        padding: 10px;
        max-width: 365px;
    }
    .todo-apps > h1 {
        font-size: 40px;
    }
    .todo-container {
        padding: 10px;
    }
    .btn-primary {
        gap: 10px;
    }
}
